<template>
    <div class="card" data-aos="zoom-in">
        <div class="primary">
            <img class="icon" src="../assets/UF_logo.jpg">
            <div class="info">
                <div class="position">{{properties.position}}</div>
                <div class="institute"><b>{{properties.institute}}</b></div>
                <div class="description">{{properties.description}}</div>
                <div class="time"><i>{{properties.time}}</i></div>
            </div>
        </div>
        
        <br>
        <div class="projects" v-if="!properties.collapsed">
            <div class='left-arrow'></div>
            <div class="project-card"> 
                <div>Lorem ipsum dolor sit amet</div>
            </div>
            <div class='right-arrow'></div>
            <div class="down-desc">projects</div>
            <img class="down" src="../assets/keyboard-down-arrow.png">
        </div>
    </div>
</template>

<script>
export default {
    props:[
        'properties'
    ]
}
</script>

<style lang="scss" scoped>

$card-height: 100px;
$orange: #e54304;
$border-radius: 10px;
$box-shadow: 0 5px 20px rgba(0,0,0,0.19), 0 3px 6px rgba(0,0,0,0.23);
$box-shadow-highlight: 0 5px 25px rgba(0,0,0,0.19), 0 3px 10px rgba(0,0,0,0.23);

.card{
    margin:10px;
    border-radius: $border-radius;
    box-shadow: $box-shadow;
    height: 2.5*$card-height;
}
.icon{
    position:absolute;
    left: 0px;
    width: $card-height;
    height: $card-height;
}
.primary{
    position:absolute;
    overflow: hidden;
    border-radius: $border-radius;
    height: $card-height;
    width: 100%;
    box-shadow: $box-shadow;
}
$margin:10px;
.info{
    position: absolute;
    left: $card-height;
    height: $card-height - 20px;
    width: calc(100% - #{$card-height+2*$margin});
    margin: $margin;
    text-align: left;
}
.position{
    font-size: 1.4rem;
    color: black;
}
.institute{
    font-size: 0.85rem;
    color: $orange;
    margin: 7px 0px;
    text-transform: uppercase;
}
.time{
    position: absolute;
    right: 5px;
    top: 5px;
    font-size: 1.0rem;
    color: $orange;
    text-transform: uppercase;
}
.projects{
    position: absolute;
    top: $card-height;
    height: 1.5*$card-height;
    padding: 0px 25px;
    width: calc(100% - 50px);
    transition: 0.3s;
}
.collapsed{
    height:20px;
}
$down-arrow-size: 30px;
.down-desc{
    color: gray;
    font-size: 0.8rem;
    text-align: center;
    position: absolute;
    bottom: $down-arrow-size;
    right:0;
    left:0;
    z-index: -1;
}
.down{
    position: absolute;
    bottom: 0px;
    left: calc(50% - #{$down-arrow-size/2});
    width: $down-arrow-size;
    cursor: pointer;
}
.project-card{
    @extend .card;
    height:$card-height;
    margin-top: 18px;
    z-index:0;
    background-color:white;
    width: 1.6* $card-height;
    padding: 5px;
    cursor: pointer;
    transition: 0.3s;
    &:hover{
        transform: scale(1.05);
        box-shadow: $box-shadow-highlight;
    }
}
.arrow{
    position: absolute;
    width: 25px;
    height: $card-height;
    top:0;
    bottom:0;
    color: gray;
    transition: 0.3s;
    padding-top: $card-height/2;
    &:after{
        // vertical-align: middle;
        content: '\02C2';
        // font-weight:5000;
    }
    &:hover{
        cursor: pointer;
        background-color: lightgray;
        color: black;
    }
}
.left-arrow{
    @extend .arrow;
    margin-right: 5px;
    left: 0;
    border-radius: 0px 0px 0px $border-radius;

}
.right-arrow{
    @extend .arrow;
    margin-left: 5px;
    right:0;
    border-radius: 0px 0px $border-radius 0px;
    &:after{
        content: '\02C3';
    }
}
</style>
